.toast {
   position: absolute;
   left: 50%;
   top: 50%;
   -webkit-transform: translate(-50%, -50%);
           transform: translate(-50%, -50%);
   width: 0px;
   padding: 20px;
   min-height: 0px;
   text-align: center;
   background: rgba(0, 0, 0, 0.5);
   border-radius: 5px;
   color: #fff;
   -webkit-transition: all 0.5s;
   transition: all 0.5s;
   z-index: -1;
   opacity: 0;
}
.toast.active {
   width: 150px;
   min-height: 25px;
   opacity: 1;
   z-index: 11;
   -webkit-animation: upDown .7s linear ;
           animation: upDown .7s linear ;
}
@-webkit-keyframes upDown{
0%{ top: 0;
}
50%{ top: 50%;
}
70%{ top: 40%;
}
100%{ top: 50%;
}
}
@keyframes upDown{
0%{ top: 0;
}
50%{ top: 50%;
}
70%{ top: 40%;
}
100%{ top: 50%;
}
}
